import React from 'react';
import {connect} from "react-redux";
import {
    setCurrentPage,
    setUsers,
    setTotalUsersCount,
    toggleIsFetching,
    unFollow, follow, toggleFollowingProgress, requestUsers,
} from "../../Redax/users-reducer";
import * as axios from "axios";
