const ADD_MESSAGE = 'ADD-MESSAGE';

let initialState={

    Dialoges: [
        {id: 1, name: "Dimych"},
        {id: 2, name: "Tanya"},
        {id: 3, name: "Liza"},
        {id: 4, name: "Artur"},
        {id: 5, name: "Katya"},
        {id: 6, name: "Evgeniy"}
    ],

        Messages: [
    {id: 1, message: "Hi, how are you?"},
    {id: 2, message: "What are you doing now?"},
    {id: 3, message: "How old are you?"}
]

}

const dialogsReducer = (state=initialState, action) => {
    switch (action.type) {
        case ADD_MESSAGE:
            let newMessage = action.newPostElement;

            return {
                ...state,
                Messages:[...state.Messages, {id:4, message: newMessage}],

            }
        default:
            return state;
    }

}

export const addMessageActionCreator=(newPostElement)=>({type:ADD_MESSAGE, newPostElement});

export default dialogsReducer;