import React from 'react';
import s from './sideBarFriends.module.css';

const NamesOfFriends = (props) => {

    return (
        <div className={s.grid}>
            <div>
                <img className={s.img}
                     src="data:image/jpeg;base64,"/>

            </div>
            <div className={s.navFriends}>
                {props.name}
            </div>
        </div>
    )
}
export default NamesOfFriends;